<template>
    <div class="order-list">
        <div class="search" :class="{active:searchType}">
            <div class="search-box">
                <el-input v-model="search.keyword" size="small" :placeholder="$L('搜索：产品名称/订单号')" class="form-input"
                          @change="doSearch">
                    <i slot="suffix" class="el-input__icon el-icon-search"></i>
                </el-input>
            </div>
            <div class="bbx-font bbx-icon-close mainFontColor" @click="showSearch(false)"></div>
        </div>
        <div class="order-content">
            <div class="search-status">
                <el-select size="small" v-model="search.timeScreening" class="form-select" @change="doSearch" v-if="$route.params.hasOwnProperty('type') && $route.params.type !== 'needPay' && $route.params.type !== 'needReceipt' && $route.params.type !== 'notShipped'">
                    <el-option :label="item.label" :value="item.value" :key="item.value"
                               v-for="item in orderTimeList"></el-option>
                </el-select>
                <el-select size="small" v-model="search.afterSaleStatus" class="form-select" @change="doSearch" v-if="$route.params.hasOwnProperty('type') && $route.params.type === 'afterSale'">
                    <el-option :label="item.label" :value="item.value" :key="item.value"
                               v-for="item in orderTypeList"></el-option>
                </el-select>
            </div>
            <div class="order-box" v-loading="loading" v-if="orderList && orderList.hasOwnProperty('list') && orderList.list.length>0">
                <div class="order-item"  v-for="(item,index) in orderList.list" :key="index">
                    <div class="head">
                        <div class="left">
                            {{ $L('订单号')}}:{{ item.sn }}
                        </div>
                        <div class="right">
                            <div v-if="item.thisOrderStatus < 0"
                                 class="color-info item-one-txt">{{$L('已关闭')}}
                            </div>
                            <div v-else-if="item.thisOrderStatus === 0 || item.thisOrderStatus === 1" class="color-danger item-one-txt">
                                {{statusFunc(item.payStatus)}}
                            </div>
                            <div v-else-if="item.thisOrderStatus === 2"
                                 class="color-warning item-one-txt">
                                {{$L('待收货')}}
                            </div>
                            <div v-else-if="item.thisOrderStatus === 3" class="color-warning item-one-txt">
                                {{$L('退货中')}}
                            </div>
                            <div v-else-if="item.thisOrderStatus === 6" class="color-warning item-one-txt">
                                {{$L('待发货')}}
                            </div>
                            <div v-else-if="item.thisOrderStatus === 4" class="color-success item-one-txt">
                                {{$L('已退货')}}
                            </div>

                            <div class="color-success item-one-txt" v-else>
                                {{$L('已完成')}}
                            </div>
                        </div>
                    </div>
                    <div class="info" v-for="(i,k) in item.orderGoods" :key="k" @click="changeOrderType(item,'info')">
                        <div class="left">
                            <img class="img" :src="i.productPic">
                        </div>
                        <div class="right">
                            <div class="top">
                                <div class="left-item">
                                    <p class="tit">{{ i.chrName }}</p>
                                    <p v-if="item.mark" class="mainFontColor">({{item.mark}})</p>
                                    <p class="font-grey" v-if="i.hasOwnProperty('goodsSpec')"><span v-html="i.goodsSpec"></span></p>
                                </div>
                                <div class="right-item">
                                    <p>{{ i.goodsPrice }}</p>
                                    <p class="font-grey" v-if="i.hasOwnProperty('sellPrice')">{{ i.sellPrice }}</p>
                                    <p>*{{ i.goodsNum }}</p>
                                </div>
                            </div>
                        </div>

                    </div>
                    <div class="bottom">
                        <p>{{ $L('共') }}{{item.goodsCount}}{{ $L('件商品') }}&nbsp;{{ $L('合计:') }}{{ item.orderAmount }}</p>
                    </div>
                    <div class="bottom-btn">
                        <el-button
                                v-if="item.hasOwnProperty('canRefund') && item.canRefund === 1"
                                @click="changeOrderType(item,'refund')"
                                class="item-btn"
                                size="small">{{$L('申请退款')}}</el-button>

                        <el-button v-if="(item.thisOrderStatus === 0 || item.thisOrderStatus === 1) && item.payStatus>=0" type="primary"
                                   size="small"
                                   class="item-btn"
                                   @click="changeOrderType(item,'pay')">{{$L('立即付款')}}</el-button>

                        <el-button v-if="item.thisOrderStatus === 2"
                                   type="primary"
                                   size="small"
                                   class="item-btn"
                                   @click="changeOrderType(item,'finish')">{{$L('确认收货')}}</el-button>

                        <el-button v-if="item.commentsStatus === 0"
                                   size="small"
                                   type="primary"
                                   class="item-btn"
                                   @click="changeOrderType(item,'evaluate')">{{$L('去评价')}}</el-button>

                        <el-button v-if="item.thisOrderStatus === 0"
                                   size="small"
                                   class="item-btn"
                                   @click="changeOrderType(item,'cancel')">{{$L('取消订单')}}</el-button>
                        <el-button
                                v-if="item.thisOrderStatus < 0 || item.thisOrderStatus == 4 || item.thisOrderStatus == 5"
                                @click="changeOrderType(item,'del')"
                                class="item-btn"
                                size="small">{{$L('删除订单')}}</el-button>

                        <el-button v-if="item.thisOrderStatus === 3 || item.thisOrderStatus === 4"
                                   size="small"
                                   class="item-btn"
                                   @click="changeOrderType(item,'afterSaleInfo')">{{$L('售后详情')}}</el-button>

                        <el-button type="text"
                                   class="item-btn"
                                   @click="changeOrderType(item,'info')">{{ $L('订单详情') }}></el-button>

                        <el-button type="text"
                                   class="item-btn"
                                   v-if="item.commentsStatus === 1"
                                   @click="changeOrderType(item,'evaluateInfo')">{{ $L('查看评价') }}></el-button>
                    </div>
                </div>
            </div>
            <div class="page">
                <el-pagination
                        v-if="orderList && orderList.hasOwnProperty('list') && orderList.list.length>0"
                        class="list-pages"
                        small
                        :background="true"
                        @size-change="sizeChange"
                        @current-change="currentChange"
                        :current-page="nowPage"
                        :page-size="pageNum"
                        layout="prev, pager, next"
                        :total="orderList.allNum">
                </el-pagination>
                <div class="no-data" v-else>
                    {{ $L('暂无数据') }}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import orderPay from "./orderPay.vue";
    export default {
        name: "orderList",
        data() {
            let pageNum = localStorage.getItem('uc_page_size');
            if (pageNum) {
                pageNum = parseInt(pageNum);
                if (isNaN(pageNum)) {
                    pageNum = 10;
                }
            } else {
                pageNum = 10;
            }
            return {
                loading:true,
                searchType: false,
                inputValue:'',
                afterSaleStatus:0,
                timeScreening:'all',
                orderTypeList: [
                    {
                        value: 0,
                        label: this.$L("全部状态")
                    },
                    {
                        value: 1,
                        label:  this.$L("退款中")
                    },
                    {
                        value: 2,
                        label:  this.$L("退款成功")
                    },
                    {
                        value: 3,
                        label:  this.$L("退款失败")
                    },
                ],
                orderTimeList: [
                    {
                        value: "all",
                        label:  this.$L("全部时间")
                    },
                    {
                        value: "seven",
                        label:  this.$L("近7天订单")
                    },
                    {
                        value: "thirty",
                        label:  this.$L("近30天订单")
                    },
                    {
                        value: "halfYear",
                        label:  this.$L("近半年订单")
                    },
                    {
                        value: "year",
                        label:  this.$L("近1年订单")
                    },
                    {
                        value: "yearAgo",
                        label:  this.$L("1年以前订单")
                    }
                ],
                pageNum: pageNum,
                nowPage: 1,
                search: {
                    afterSaleStatus: 0,
                    timeScreening: "all",
                    keyword: ''
                },
                orderList:{},
                finishType:true
            }
        },
        computed: {
            //用于读取和设置顶部按钮
            mobileTopBtn: {
                get() {
                    return this.$store.state.mobileTopBtn;
                },
                set(val) {
                    this.$store.commit('setMobileTopBtn', val);
                }
            }
        },
        beforeRouteUpdate(to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 执行设置顶部按钮
            this.nowPage = 1;
            this.search = {
                afterSaleStatus: 0,
                timeScreening: "all",
                keyword: ''
            };
            this.getData(to.params.hasOwnProperty('type') ? to.params.type : 'all');
            this.setupTopBtn();
            this.showSearch(false);
            next();
        },
        created() {
            //创建当前页面时执行设置顶部按钮
            this.setupTopBtn();
            this.getData(this.$route.params.hasOwnProperty('type') ? this.$route.params.type : 'all');
        },
        beforeDestroy() {
            //销毁当前页面时，必须清空顶部按钮，不然会影响其他页面
            this.mobileTopBtn = null
        },
        methods: {
            statusFunc (status){
                let x=this.$L('待付款');
                switch(status){
                    case -1:
                        x = this.$L('货到付款');
                        break;
                    case -2:
                        x = this.$L('到店付款');
                        break;
                    case -3:
                        x = this.$L('等待汇款');
                        break;
                }
                return x;
            },
            setupTopBtn() {
                this.mobileTopBtn = [
                    {
                        icon: 'bbx-icon-sousuo',
                        size: '24px',//图标大小，可以不设置，默认24px
                        func: () => {
                            this.showSearch(true);
                        }
                    }
                ];
            },
            showSearch(type) {
                if (type) {
                    //显示搜索框内容
                    this.searchType = true;
                } else {
                    //隐藏搜索框内容
                    this.searchType = false
                }
            },
            // searchFunc(val){
            //     this.inputValue = val;
            //     this.getData();
            // },
            // timeSearchFunc(val){
            //     this.timeScreening =val;
            //     this.getData();
            // },
            // changeSaleFunc(val){
            //     this.afterSaleStatus =val;
            //     this.getData();
            // },
            reloadData(){
                this.getData(this.$route.params.hasOwnProperty('type') ? this.$route.params.type : 'all');
            },
            sizeChange(num) {
                this.pageNum = num;
                localStorage.setItem('uc_page_size', num);
                this.getData(this.$route.params.hasOwnProperty('type') ? this.$route.params.type : 'all');
            },
            currentChange(num) {
                this.nowPage = num;
                this.getData(this.$route.params.hasOwnProperty('type') ? this.$route.params.type : 'all');
            },
            doSearch() {
                this.getData(this.$route.params.hasOwnProperty('type') ? this.$route.params.type : 'all');
            },
            getData(type) {
                const _self = this;
                _self.loading = true;
                let postData = {
                    type: type,
                    keyword: _self.search.keyword,
                    p: _self.nowPage,
                    num: _self.pageNum,
                    afterSaleStatus: _self.search.afterSaleStatus,
                    timeScreening: _self.search.timeScreening,
                };
                _self.$api('getOrderList', postData, function (reData) {
                    if (reData.flag === 1) {
                        var alldata = reData.data.list;
                        for (var i in alldata){
                            var orderGoods = alldata[i]['orderGoods'];
                            var goodsCount = 0;
                            for (var ii in orderGoods){
                              var goodsNum = orderGoods[ii]['goodsNum'];
                              goodsCount += goodsNum;
                            }
                            alldata[i]['goodsCount'] = goodsCount;
                        }
                        reData.data.list = alldata;
                        _self.orderList = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getData();
            },
            handleSizeChange(val) {
                this.pageSize = val;
                this.getData();
            },

            changeOrderType(item, status) {
                switch (status) {
                    case 'del':
                        //删除订单
                        if (item.thisOrderStatus < 0 || item.thisOrderStatus == 4 || item.thisOrderStatus == 5) {
                            this.$confirm(this.$L('是否确认删除订单？'), this.$L('删除订单'), {
                                confirmButtonText: this.$L('立即删除'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('changeType', {
                                    orderId: item.orderId,
                                    orderType: 'del'
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.reloadData();
                                    } else {
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'refund':
                        if(item.hasOwnProperty('canRefund') && item.canRefund === 1){
                            //申请售后
                            this.$router.push({name: "orderService", params: {id: item.orderId}});
                            break;
                        }
                        break;
                    case 'pay':
                        //立即付款
                        if ((item.thisOrderStatus === 0 || item.thisOrderStatus === 1) && item.payStatus >= 0) {
                            this.$store.commit('setDialogObj', {
                                name: this.$L('订单支付'),
                                view: orderPay,
                                width: '100%',
                                loading: true,
                                data:{
                                    orderId: item.orderId,
                                }
                            });
                        }
                        break;
                    case 'cancel':
                        //取消订单
                        if (item.thisOrderStatus === 0) {
                            this.$confirm(this.$L('确认取消订单吗？'), this.$L('取消订单'), {
                                confirmButtonText: this.$L('立即取消'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('changeType', {
                                    orderId: item.orderId,
                                    orderType: 'cancel'
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.reloadData();
                                    } else {
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'finish':
                        //确认收货
                        if (item.thisOrderStatus === 2) {
                            this.$confirm(this.$L('是否确认收货？'), this.$L('确认收货'), {
                                confirmButtonText: this.$L('立即确认'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('changeType', {
                                    orderId: item.orderId,
                                    orderType: 'finish'
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.reloadData();
                                    } else {
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'evaluate':
                        //去评价
                        if (item.commentsStatus === 0) {
                            this.$router.push({name: "orderComment", params: {id: item.orderId}});
                        }
                        break;
                    case 'afterSaleInfo':
                        //售后详情
                        if (item.thisOrderStatus === 3 || item.thisOrderStatus === 4) {
                            this.$router.push({name: "orderServiceInfo", params: {id: item.orderId}});
                        }
                        break;
                    case 'evaluateInfo':
                        //评价详情
                        if (item.commentsStatus === 1) {
                            this.$router.push({name: "checkComment", params: {id: item.orderId}});
                        }
                        break;
                    case 'info':
                        //订单详情
                        this.$router.push({name: "orderInfo", params: {id: item.orderId}});
                        break;
                }
            },
            showLoad(type) {
                if (typeof type === "undefined" || type) {
                    this.loading = this.$loading({lock: true});
                } else {
                    if (this.loading) {
                        this.loading.close();
                        this.loading = null;
                    }
                }
            },
            reloadData(){
                this.getData(this.$route.params.hasOwnProperty('type') ? this.$route.params.type : 'all');
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../../assets/css/common/lib.scss';

    .order-list {
        background: #eff1f5;
        .search {
            position: relative;
            width: 100%;
            height: 0;
            line-height: 50px;
            text-align: center;
            overflow: hidden;
            @include diy-animation-show(height);
            border-bottom: 1px solid #F2F6FC;

            &.active {
                height: 50px;
                padding: 0 10px;
                background: #fff;
            }

            .bbx-font {
                position: absolute;
                width: 50px;
                height: 50px;
                line-height: 50px;
                text-align: center;
                top: 0;
                right: 0;
                font-size: 20px;
            }
            .search-box{
                width: calc(100% - 40px);
                /deep/ .el-input__inner{
                    padding: 0 10px;
                }
                /deep/ .el-input-group__append{
                    padding: 0 12px;
                    background: #fff;
                    color: #333;
                    font-size: 16px;
                }
            }
        }
        .order-content{
            width: 100%;
            background: #eff1f5;
            .search-status{
                line-height: 52px;
                padding: 0 10px;
                border-bottom: 1px solid #F2F6FC;
                background: #fff;
                .el-select{
                    margin-right: 10px;
                    width: 110px;
                    /deep/ .el-input__inner{
                        padding: 0 10px;
                    }
                }
            }
            .order-box{
                .order-item{
                    background: #fff;
                    padding: 10px;
                    margin: 10px 0;
                    .head{
                        height: 20px;
                        line-height: 20px;
                        .left{
                            float: left;
                            display: inline-block;
                            height: inherit;
                            line-height: inherit;
                        }
                        .right{
                            float: right;
                            display: inline-block;
                            height: inherit;
                            line-height: inherit;
                            .color-info{
                                color: #909399;
                            }
                            .color-danger {
                                color: #F56C6C;
                            }
                            .color-warning {
                                color: #E6A23C;
                            }
                            .color-success {
                                color: #67C23A;
                            }
                        }
                    }
                    .info{
                        margin: 10px 0;
                        height: 90px;
                        border-bottom: 1px solid #eff1f5;
                        cursor: pointer;
                        .left{
                            display: inline-block;
                            vertical-align: top;
                            width: 80px;
                            .img{
                                width: inherit;
                                height: 80px;
                                &:hover{
                                    opacity: 0.8;
                                }
                            }
                        }
                        .right{
                            display: inline-block;
                            vertical-align: top;
                            width: calc(100% - 80px);
                            .top{
                                .left-item{
                                    padding-left: 10px;
                                    display: inline-block;
                                    vertical-align: top;
                                    width: 50%;
                                    .tit{
                                        cursor: pointer;
                                        overflow: hidden;
                                        display: -webkit-box;
                                        text-overflow: ellipsis;
                                        -webkit-line-clamp: 2;
                                        -webkit-box-orient: vertical;
                                        white-space: normal;
                                        font-size: 12px;
                                    }
                                    .font-grey{
                                        color: #999;
                                    }
                                }
                                .right-item{
                                    display: inline-block;
                                    vertical-align: top;
                                    width: 50%;
                                    text-align: right;
                                    .font-grey{
                                        color: #999;
                                        text-decoration: line-through;
                                    }
                                }
                            }

                        }
                        &:last-child{
                            margin-bottom: 0 !important;
                        }
                    }

                    .bottom{
                        text-align: right;
                        margin-bottom: 10px;
                    }
                    .bottom-btn{
                        text-align: right;
                    }
                }
            }

            .page{
                background: #fff;
                padding: 20px 0;
                text-align: center;
            }
        }
        .no-data{
            position: relative;
            width: 100%;
            height: 170px;
            line-height: 170px;
            text-align: center;
            background-color: #ffffff;
        }
    }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>